<template>
  <v-data-table class="elevation-0 suuma-table" :headers="headers" :items="usuarios" item-key="id">
    <template v-slot:no-data>
      <v-alert :value="true" color="error" icon="warning">No hay usuarios por aprobar</v-alert>
    </template>
    <template v-slot:items="props">
      <td class="text-xs-left">{{ props.item.email }}</td>
      <td class="text-xs-center">
        <v-flex xs12 sm6 d-flex>
          <v-select 
          :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
          :items="ranges" v-model="props.item.range"></v-select>
        </v-flex>
      </td>
      <td class="text-xs-center">
        <v-checkbox
          hide-details
          class="align-center justify-center"
          :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
          v-model="props.item.instructor"
        ></v-checkbox>
      </td>
      <td class="text-xs-center">
        <v-checkbox
          :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
          hide-details
          class="align-center justify-center"
          v-model="props.item.operator"
        ></v-checkbox>
      </td>
      <td class="text-xs-center">
        <v-checkbox
          :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
          hide-details
          class="align-center justify-center"
          v-model="props.item.service_chief"
        ></v-checkbox>
      </td>
      <td class="text-xs-center">
        <v-switch 
        :disabled="(props.item.email === 'admin@suuma.com.mx') ? true : false"
        hide-details class="align-center justify-center" v-model="props.item.isActive"></v-switch>
      </td>
      <td class="text-xs-center">
        <v-btn
          small
          depressed
          color="info"
          @click="patch(props.item.id)"
          :disabled="!props.item.changed"
        >Guardar</v-btn>
      </td>
    </template>
  </v-data-table>
</template>
<script>
export default {
  async created() {
    this.$store.commit("SET_LOADING", {
      appear: true,
      message: "Obteniendo usuarios..."
    });
    await this.fetchUsers();
    this.$store.commit("SET_LOADING", { appear: false, message: "" });
  },
  data() {
    return {
      tipos_usuarios: ["Admin", "Operador"],
      ranges: ["TAM", "CP"],
      headers: [
        { text: "Correo", value: "email", sortable: false, align: "left" },
        {
          text: "Rango",
          value: "aprobar",
          sortable: false,
          align: "center",
          width: "1%"
        },
        {
          text: "Instructor",
          value: "instructor",
          sortable: false,
          align: "center"
        },
        {
          text: "Operador",
          value: "operator",
          sortable: false,
          align: "center"
        },
        {
          text: "Jefe de Servicio",
          value: "service_chief",
          sortable: false,
          align: "center"
        },
        // { text: "Grupo", value: "user_group", sortable: false },
        { text: "Activar", value: "approve", sortable: false, align: "center" },
        { text: "Acciones", value: "save", sortable: false, align: "center" }
        // { text: "Avanzado", value: "advanced", sortable: false, align: "center"}
      ],
      usuarios: [],
      usuariosCopy: []
    };
  },
  watch: {
    usuarios: {
      handler: function(e) {
        if (e.length == 0) return;
        let changes = new Array(this.usuarios.length).fill(0);
        let id = 0;
        e.forEach(element => {
          let keys = Object.keys(element);
          for (const key in keys) {
            if (
              e[id][keys[key]] !== this.usuariosCopy[id][keys[key]] &&
              keys[key] != "changed"
            ) {
              changes[id] += 1;
            }
          }
          id += 1;
        });
        id = 0;
        for (const change of changes) {
          if (change != 0) {
            this.usuarios[id].changed = true;
          } else {
            this.usuarios[id].changed = false;
          }
          id += 1;
        }
      },
      deep: true
    }
  },
  methods: {
    async patch(id) {
      let id_real = 0;
      this.usuarios.some((el, idx) => {
        if (el.id === id) {
          id_real = idx;
          return true;
        }
      });
      try {
        let response = await this.$http.patch(
          "aprobar/usuarios/" + id,
          this.usuarios[id_real]
        );
        let user = response.data.data;
        this.usuarios[id_real].changed = false;
        this.usuariosCopy[id_real] = this.$deepCopy(this.usuarios[id_real]);
      } catch (error) {
        console.log(error);
      }
    },
    async fetchUsers() {
      try {
        let notActive = await this.$http.get("aprobar/usuarios");
        if (notActive.data.data.length > 0) {
          for (const user in notActive.data.data) {
            // Casting
            notActive.data.data[user].changed = false;
            notActive.data.data[user].instructor =
              notActive.data.data[user].instructor == 1 ? true : false;
            notActive.data.data[user].operator =
              notActive.data.data[user].operator == 1 ? true : false;
            notActive.data.data[user].service_chief =
              notActive.data.data[user].service_chief == 1 ? true : false;
            notActive.data.data[user].isActive =
              notActive.data.data[user].isActive == 1 ? true : false;
          }
          this.usuarios = notActive.data.data;
          this.usuariosCopy = this.$deepCopy(notActive.data.data);
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

<style>
.fixSelect {
  /* overflow-y: auto; */
  overflow: scroll;
}
</style>
